.hero{
 display: flex;
 flex: 1 1;
 flex-direction: row ;   
}
.left-h{
    display: flex;
     flex-direction: column;
     /* gap: 0.5rem; */
     /* width: 40rem; */
     /* background-color: blue; */
     padding: 5rem;
     line-height: 4rem;

}
.left-h>:nth-child(1){
    color: #6c757d; 
}
.left-h>:nth-child(2){
    color: #050606; 
    font-size: 4rem;
    font-weight: 900;
}
.left-h>:nth-child(3){
    color: #050606; 
    font-size: 4rem;
    font-weight: 900;
}
.left-h>:nth-child(4){
    color: #dc8cdb; 
    font-size: 4rem;
    font-weight: 900;
}
.left-h> button{
    background:  linear-gradient(-145deg, rgb(219, 138, 222) 0%, rgb(246, 191, 159) 100%)  !important ;
    width: 7rem;
    height: 2.5rem;
    margin-top: 3rem;
    color: white;
    /* text-transform: uppercase; */
    border: none;
    border-radius: 5px;
}
.hero>img{
    position: absolute;
    z-index: -3;
    margin-top: 19.1rem;
}
.right-h>:nth-child(1){
    height: 27rem;
    padding: 5rem;
}
.right-h>:nth-child(2){
    position: absolute;
    z-index: -3;
    right: 1rem;
    margin-top: 6rem;
}










@media(max-width:778px) {
    .hero{
        display: flex;
        flex: 1 1;
        flex-direction: column ;   
       }
       .left-h{
        display: flex;
         flex-direction: column;
         /* margin-top: 1rem; */
         /* gap: 0.5rem; */
         /* width: 40rem; */
      
         padding: 1.5rem;
         text-align: center;
         justify-content: center;
         /* line-height: 4rem; */
         /* top: 7rem; */
    
    }
    .left-h>:nth-child(1){
        color: #6c757d; 
top: 0rem;
        /* margin-top: 1rem; */
    }
    .left-h> button{
        background:  linear-gradient(-145deg, rgb(219, 138, 222) 0%, rgb(246, 191, 159) 100%)  !important ;
        /* width: 7rem; */
        height: 2.5rem;
        /* margin-top: 3rem; */
        color: white;
        text-align: center;
        justify-content: center;
        /* text-transform: uppercase; */
        border: none;
        border-radius: 5px;
        margin-left: 6rem;
    }
    .hero>img{
        position: absolute;
        z-index: -3;
        margin-top: 12rem;
    }
    .right-h{
        justify-content: center;
        text-align: center;

    }
    .right-h>:nth-child(1){
        height: 19rem;
        justify-content: center;
        padding: 2rem;
        margin-top: 3rem;
    }
    .right-h>:nth-child(2){
        position: absolute;
        z-index: -3;
        right: 0rem;
       
        /* padding: 2rem; */
        margin-top: 5rem;
    }
}
