
.Services{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    margin-top: 5rem;
    padding: 5rem;
}
.Services>div{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    font-size: 1.5rem;
    font-weight: 800;
    padding: 0.6rem;
    text-align: center;
   
    justify-content: center;
}
.Services>span{
    color: #6c757d; 
    font-size: 0.8rem;
    letter-spacing: 0.1rem;
}
.purple{
    color: #dc8cdb;
}
.i-slide{
    margin-top: 3rem;
    /* height: 20rem; */
    /* background-color: red; */
}
.mySwiper{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 3rem;
}
.slide-container{
    width: 14.5rem;
height: 19rem;
border-radius: 1rem;
/* background-color: pink; */
display: flex;
flex-direction: column;
gap: 1rem;
justify-content: center;
text-align: center;
}
.slide-container:hover{
   
}
.slide-container>img{
    height: 4rem;
    width: 4rem;
    /* top: 1rem; */
    margin: 0rem 5rem;
    /* padding: 4rem; */
}
.slide-container>span{
    font-size: 1.2rem;
font-weight: 900;
color: black;
font-style: normal;
}
.slide-container>div{
    
}
.mySwiper2{
    display: none;
}


