.header{
    /* position: sticky; */
    position: sticky;
    top: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex: 1 1 ;
    background: linear-gradient(-145deg, rgb(219, 138, 222) 0%, rgb(246, 191, 159) 100%)  !important ;
    color: white;
    z-index: 100;
    height: 5rem;
}
.head-com{
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    height: 5rem;
    padding: 1.7rem;
    gap: 2rem;
    color: white;
    
    padding-left: 12rem;
}
.head-com>a{
    text-decoration: none;
    display: block;
    color: white;
    cursor: pointer;
}
.head-com>div{
    color: white;
    text-decoration: none;
    /* display: block; */
}
.header >img{
   height: 2.5rem;
   padding: 1.5rem 5rem;
}
.tog{
    display: none;
}













