    .header{
        /* position: sticky; */
        /* display: flex; */
        
        background: linear-gradient(-145deg, rgb(219, 138, 222) 0%, rgb(246, 191, 159) 100%)  !important ;
    width: 100%;
        z-index: 100;
        height: 5rem;

    }
    .head-com{
        position: absolute;
