        margin-top: 5rem;
        right: 1.4rem;
        gap: 2rem;
        background-color: #e9ecef;
    }
    .head-com>div{
        border-bottom: 1px solid #6c757d;;

    }
    .mainnav_before{
        /* background-color: red; */
