}
.courses>:nth-child(1){
    position: absolute;
    margin-top: 0rem;
    left: 0rem;
    z-index: -3;
}
.courses>:nth-child(2){
    position: absolute;
    margin-top: 0rem;
    right: 0rem;
    z-index: -3;
}
.c-content{
    display: flex;
    flex-direction: row;
    flex: 1 2;
    gap: 2rem;
}
.left-c{
display: flex;
flex-direction: column;
gap: 2rem;
margin-left: 5rem;
margin-top: 7rem;
}
.left-c>button{
    font-size:  1.1rem;
    display: flex;
    flex-direction: row;
    font-weight: 700;
    color: black;
    background: white;
    border: 4px solid #cc97cb;
    padding:  1.4rem;
    border-radius: 5px;
    width: 13rem;
}
.left-c>button:hover{
    color: #dc8cdb;
    background: white;
    border: 4px solid #dc8cdb;
}
.right-c{
    margin-top: 5rem;
    /* margin-left: 9rem; */
    /* background-color: black; */
}
.com{
    display: flex;
    flex-direction: row;
    box-shadow: 2px 5px 6px 8px #eff0f1;
    border-radius: 6px ;
    /* left: 5rem; */
    margin-left: 10rem;
    margin-top: 3rem ;
    z-index: 3;
}
.com-inner{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-left: 1rem;
    margin-top: 1rem;
    
}
.com-inner>:nth-child(1){
    font-size: 1.2rem;
    font-weight: 800;
    /* padding: 1rem; */
}
.com>img{
    padding: 1 0rem;
}
.com-inner>:nth-child(2){
    font-size: 0.9rem;
    color: #6c757d;
    /* font-weight: 800; */
    /* padding: 0rem 1rem; */
}
.com-inner>:nth-child(3){
    font-size: 0.9rem;
    color: #6c757d;
    /* font-weight: 800; */
    /* padding: 0rem 1rem; */
}
.com-inner>:nth-child(4){
    font-size: 1.1rem;
    color: #494c4e;
    /* font-weight: 800; */
    /* padding: 0rem 1rem; */
}
.com-inner>:nth-child(5){
    font-size: 0.9rem;
    color: #dc8cdb;
    /* font-weight: 800; */
    /* padding: 0rem 1rem; */
}





/* BANNER */
.banner-m{
    position: 5rem;
    position: absolute;
    margin-top: 11rem;
    width: 100%;
    /* flex: 1 1; */
    justify-content: center;
    height: 32rem;
    background: linear-gradient(-145deg, rgb(219, 138, 222) 0%, rgb(246, 191, 159) 100%)  !important ;
}
.banner-m>div{
    display: flex;
    flex-direction: row ;
    gap: 3rem;
}
.banner-m>div>img{
    /* padding: 4rem; */
    /* width: 5rem;
    height: 5rem; */
    border-radius: 20px;
    margin: 5rem;
    margin-top: 5rem;
}
.banner-con{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 28rem;
    margin-top: 3rem;
    color: white;
}
.banner-con>:nth-child(1){
    font-size: 1rem;
    font-weight: 500;
}
.banner-con>:nth-child(2){
    font-size: 3.7rem;
    font-weight: 800;
}
.banner-con>:nth-child(3){
    font-size: 0.9rem;
    font-weight: 400;
}
.banner-con>button{
    border: transparent;
    background: white;
    border-radius: 5px;
    width: 8rem;
    margin-top: 3rem;
    height: 3rem;
    color: #dc8cdb;
}



